<script setup lang="ts">
import { EVENT_STATUS } from "~/constants";
import EventsAPI from "~/services/events";

const route = useRoute();

onMounted(() => {
  if (route.query.idType !== "referenceId")
    return showError({ statusCode: 404, data: { context: "page" } });

  EventsAPI.loadById(route.params.referenceId as string, route.query.type as TEventType, route.query.idType as TIdType)
    .then((event) => {
      if (event.eventStatus === EVENT_STATUS.CHANNEL) {
        navigateTo({ path: `/channels/${event.id}`, query: { type: route.query.type } });
        return;
      }

      navigateTo({ path: `/events/${event.id}`, query: { type: route.query.type } });
    })
    .catch(() => showError({ statusCode: 404, data: { context: "page" } }));
});
</script>

<template>
  <div class="main-container text-main">
    Redirecting ...
  </div>
</template>
